import '@/common/iconfont.css';
import uniSegmentedControl from '../../components/uni-segmented-control/uni-segmented-control.vue';
export default {
	data() {
		return {
			items: ['详细', '目录'],
			current: 0,
			chapterList: [],
			book_id: 0,
			max_chapter_num: 0,
			min_chapter_num: 0,
			collectBook: false,
			scrollTop: 0,
			cover:'',
		};
	},
	methods: {
		onClickItem(e) {
			if (this.current !== e.currentIndex) {
				this.current = e.currentIndex;
			}
		},
		choiceBook(flag) {
			this.collectBook = flag;
		}
	},
	components: {
		uniSegmentedControl
	},
	onLoad(option) {
		this.cover = option.cover
		uni.request({
			url: 'http://192.168.2.100/showChapterlist/' + option.id, //仅为示例，并非真实接口地址。
			data: {},
			header: {},
			success: res => {
				this.chapterList = res.data;
				this.min_chapter_num = this.chapterList[0].pk;
				this.max_chapter_num = this.chapterList[this.chapterList.length - 1].pk;
			}
		});
	},
	onPageScroll(e) {
		this.scrollTop = e.scrollTop;
	}
};
